import React from "react";
import { Link } from 'react-router-dom'

function convertDate(dates) {
  const month = new Date(dates).getMonth()+1;
  const date = new Date(dates).getDate();
  const day = new Date(dates).getDay();
  let convertDay = '일'
  switch(day) {
    case 1:
      convertDay = '월';
      break;
    case 2:
      convertDay = '화';
      break;
    case 3:
      convertDay = '수';
      break;
    case 4:
      convertDay = '목';
      break;
    case 5:
      convertDay = '금';
      break;
    case 6:
      convertDay = '토';
      break;
    default:
      convertDay = '일';
      break;
  }
  return `${month}/${date} (${convertDay})`;
}

const Card = ({item}) => (
  <tr>
    <td>
      <Link to={`/myclass/detail?id=${item.id}`}>{item.region}</Link>
    </td>
    <td>
      <Link to={`/myclass/detail?id=${item.id}`}>
        { item.school.includes('중학교') ? item.school.split('학교')[0] : item.school.split('등학교')[0]}
        { !item.feedback_uploaded ? <span className="no-uploaded">•</span> : null}
      </Link>
    </td>
    <td>
      <Link to={`/myclass/detail?id=${item.id}`}>
        {convertDate(item.date)}
      </Link>
    </td>
    <td>
      <Link to={`/myclass/detail?id=${item.id}`}>{item.program}</Link>
    </td>
    <td>
      <Link to={`/myclass/detail?id=${item.id}`}>{item.status}</Link>
    </td>
  </tr>
)

export default Card
